<template>
    <div>
        <div id="header" class="row">
            <h3>Chat</h3>
            <button class="btn waves-effect waves-light blue darken-1"
                    type="submit"
                    v-on:click="closeSession"
                    name="action">
                Cerrar Sessión
            </button>
        </div>

        <div class="row">
            <div class="col s3" id="Contactos">
                <h2>Contactos</h2>
                <Contactos v-bind:contactos="contactos" v-on:click-set-chat="setChat"></Contactos>

                <button
                    class="btn waves-effect waves-light blue darken-1"
                    v-on:click="NewContact">Nuevo contato</button>

            </div>
            <div class="col s9" id="chat" >
                <h2>chat</h2>
                <div v-if="isSelected">
                    <h2>{{ chat.name  }}</h2>
                    <div v-bind:key="msg.id" v-for="msg in chat.mensajes"> {{ msg  }}</div>
                    <input type="text" v-model="mensaje" v-on:keydown.enter="insertarmsg" />
                </div>
                <div v-else>
                    <h2>Chatea con tus contactos</h2>
                </div>

            </div>
        </div>

    </div>
</template>

<script >
 import {uuid} from 'vue-uuid'
 import Contactos from './Contactos.vue'
 export default {
     name: 'Dashboard',
     components: { Contactos },
     data() {
         return {
             user: {},
             contactos: [],
             chat: {},
             isSelected: false,
             mensaje: '',
         }
     },
     methods: {
         closeSession() {
             this.$cookies.remove('Token')
             this.$router.push('/')
         },
         setChat(id) {
             // buscamos el elemento por medio del id
             this.contactos.findIndex( element => {
                 if (element.id == id ) {
                     this.chat = element
                     return true
                 }
             })
             this.isSelected = true
         },
         insertarmsg() {
             const newmensaje = {
                 id: uuid.v4(),
                 mensaje: this.mensaje
             }
             this.chat.mensajes.push(newmensaje)
             this.mensaje = ''
         },
         NewContact() {
             console.log('Creando un nuevo contacto')
             var name = prompt('Nombre del contacto?')
             this.$chat.post(`/users/${this.user.ID}/contacts`, {
                 number: "0997454001",
                 name: name,
                 nickname: "pepe rata",
             }).then(res => {
                 console.log(res)
                 this.GetContactos()
             })
         },
         GetContactos() {
             this.$chat.get(`/users/${this.user.ID}/contacts`)
                 .then(res => this.contactos = res.data)
         }
     },
     created() {
         var Token = this.$cookies.get('Token')
         if (Token === null) {
             this.$router.push('/')
             return
         }
         this.$chat.defaults.headers.common['Token'] = Token

         this.$chat.get('/users/me')
             .then(res => {
                 this.user = res.data
                 this.GetContactos()

             })



     }
 }
</script>
