<template>
    <div class="container">
        <h2>Crea tu cuenta</h2>
        <form v-on:submit.prevent="Register">
            <div class="row">
                <div class="input-field col s6">
                    <input type="text" id="nombre" v-model="user.name"/>
                    <label for="nombre">Nombre</label>
                </div>
                <div class="input-field col s6">
                    <input type="password" id="password" v-model="user.password"/>
                    <label for="password">Password</label>
                </div>
                <div class="input-field col s12">
                    <input type="email" id="email" v-model="user.email"/>
                    <label for="email">Email</label>
                </div>
                <div class="input-field col s6">
                    <input type="text" id="number" v-model="user.number"/>
                    <label for="number">Number</label>
                </div>

                <div class="input-field col s6">
                    <input type="text" id="role" v-model="user.role"/>
                    <label for="role">Role</label>
                </div>

                <button v-bind:class="disable ? 'disabled': '' " class="btn waves-effect waves-light blue darken-1" type="submit" name="action">Registrate
                </button>
            </div>
        </form>
    </div>
</template>

<script>

 export default {
     name: 'Registrar',
     data() {
         return {
             user: {
                 name: '',
                 password: '',
                 number: '',
                 email: '',
                 role: ''
             },
             disable: false
         }
     },
     methods: {
         Register() {
             if (this.disable) {
                 return
             }
             this.disable = true
             this.$chat.post('/users', this.user)
                 .then(res => {
                     console.log(res.data)
                     if (res.status == 200) {
                         if (!res.data.iserror) {
                             alert(res.data.message)
                             this.$router.push('/')
                         } else {
                             alert(res.data.message)
                         }
                     }
                     this.disable = false
                 })
                 .catch(err => console.log(err))

         }
     }
 }

</script>

